
.home-container {
    overflow: hidden;
}

p {
    margin: 0;
    padding: 0;
}

.about-me-header {
    padding: 0;
    margin: 0;
    display: flex;
    height: 105vh;
    width: 100vw;
}

/* About me text Style ---------------------------- */
.about-me-name {
    display: flex;
    overflow: hidden;
    background-color: #cce3de;
    flex-direction: column;
    width: 50%;
    height: 100%;
    gap: 3em;
    z-index: -1;
}

.name-text {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 7.5em;
    color: #6b9080;
    display: flex;
    text-decoration: underline;
}

.first {
    justify-content: left;
    padding-left: 10%;
}

.last {
    justify-content: right;
    padding-right: 10%;
}

.socials {
    position: absolute;
    bottom: 10px;
    display: flex;
    gap: 2em;
    margin: auto;
}

.tool {
    color: white;
    position: absolute;
    top: -3em;
    font-size: 1.5em;
    right: -4.5em;
}

.social-img {
    height: 3em;
    width: 3em;
    background-color: white;
    padding: 1em;
    border-radius: 20px;
    box-shadow: 0 0 1em black;
    box-shadow: 0 0 2em #6b9080;
    border: 1px solid #6b9080;
    transition: all;
    transition-duration: 1s;
}

.trans {
    transition: all;
    transition-duration: 1s;
}

.social-img:hover {
    cursor: pointer;
    border: 1px dashed #6b9080;
    box-shadow: 0 0 2em #c7e1d6;
}

.social-img-right:hover {
    transform: scale(1.1) rotate(7deg) translateY(-0.2em);
}

.social-img-center:hover {
    transform: scale(1.1) translateY(-0.5em);
}

.social-img-left:hover {
    transform: scale(1.1) rotate(-7deg) translateY(-0.2em);
}


/* About me Img Style ------------------------*/

.about-me-img {
    width: 50%;
    height: 100%;
    background-image: linear-gradient(to right, #cce3deff, rgba(0, 0, 0, 0.5) 60%), url('../images/codeBackground.jpg');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
}

.headshot {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/* About me description ------------------- */

.about-me-description {
    font-family: 'Carrois Gothic', serif, Arial, Helvetica, sans-serif;
    height: auto;
    color: #6b9080;
    font-weight: 500;
    text-align: center;
    font-size: 2em;
    padding: 0 1em;
    line-height: 1.2em;
    display: inline-block;
    text-align: left;
    margin: 0 auto;
}

/* Projects Section ------------------------------ */

.projects {
    height: 105vh;
    width: 100vw;
    background-color: #6b9080;
    background-image: linear-gradient(45deg, #6b9080 43.48%, #a4c3b214 43.48%, rgba(164, 195, 178, 0.663) 50%, #6b9080 50%, #6b9080 93.48%, #a4c3b214 93.48%, #a4c3b214 100%);
    background-size: 32.53px 32.53px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.project-section-title {
    padding: 2rem;
    text-align: center;
    padding-top: .7em;
    font-size: 4.5rem;
    font-family: 'Josefin Slab', serif, Arial, Helvetica, sans-serif;
    color: #cce3de;
    text-decoration: underline;
}

.project-cards {
    display: flex;
    width: 70%;
    height: 75%;
    justify-content: center;
    box-shadow: .1em .1em .5em black;

}

.swiper {
    width: 100%;
    height: 100%;
}

.slide-img {
    aspect-ratio: 1 / 1;
    width: 100%;
}

.swiper-pagination-bullet {
    width: 12px !important;
    height: 12px !important;
    background-color: #a4c3b2 !important;
    opacity: 1 !important;
  }
  
  .swiper-pagination-bullet-active {
    background-color: #f6fff8 !important;
  }

.swiper-button-next,
.swiper-button-prev {
  color: black !important;
  font-size: .5em;
  text-shadow: -1px -1px 0 #ffff, 1px -1px 0 #ffff, -1px 1px 0 #ffff, 1px 1px 0 #ffff;
}

.projects-page-div {
    background-color: #d4e2df;
    padding: .3em;
    border-radius: 15px;
    box-shadow: .2em .2em .2em rgb(79, 78, 78);
    border: 1px solid #445d53;
    transition: all;
    transition-duration: 0.5s;
    background-position: center center;
    cursor: pointer;
}

.projects-page-div:hover{
    transform: scale(1.05) rotateX(20deg);
    box-shadow:  .3em .3em .3em rgb(79, 78, 78);
}

.bounce-div {
    margin-top: 1em;
}

.project-page-button {
    color: #6b9080;
    font-size: 3em;
    text-decoration: none;
}



/* Skills Section -------------------------------------- */

.skills {
    height: 105vh;
    width: 100vw;
    background-image: linear-gradient(45deg, #cce3de 43.48%, #a4c3b2a8 43.48%, #a4c3b2a8 50%, #cce3de 50%, #cce3de 93.48%, #a4c3b2a8 93.48%, #a4c3b2a8 100%);
    background-size: 32.53px 32.53px;
    display: flex;
    flex-direction: column;
}

.my-skills-title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-top: .7em;
    font-size: 4.5rem;
    font-family: 'Josefin Slab', serif, Arial, Helvetica, sans-serif;
    color: #6b9080;
    text-decoration: underline;
}

.learning-container {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: .5em;
}

.learning-now-text {
    font-size: 4em;
    color: #6b9080;
    font-family: 'Josefin Slab', serif, Arial, Helvetica, sans-serif;
    font-weight: 700;
}

.learning-now-text-left {
    margin-right: 1em;
}

.learning-now-text-right {
    margin-left: 1em;
}

.learning-slider {
    width: 15em;
}

.learning-slider .swiper-slide {
    transition: all 0.6s;
}

.learning-slider .swiper-slide-prev,
.learning-slider .swiper-slide-next {
transform: scale(0.3);
opacity: 0.6;
}

.scroll-y {
    overflow-y: scroll;
    overflow-x: hidden;
}























/* Media Queries -----------------------*/

@media (max-width: 950px) {
    .name-text {
        font-size: 5em;
    }

    .about-me-description {
        font-size: 1.6em;
    }

    .learning-now-text {
        font-size: 3em;
    }

    .project-page-button{
        font-size: 2.5em;
    }

    .learning-now-text {
        font-size: 3.7em;
    }

    .learning-now-text {
        font-size: 3em;
    }

    .learning-now-text-left {
        margin-right: 0em;
    }
    
    .learning-now-text-right {
        margin-left: 0em;
    }
}

@media (max-width: 850px) {
    .about-me-description {
        font-size: 1.2em;
    }

    .learning-now-text {
        font-size: 2.5em;
    }

    .project-page-button{
        font-size: 2.2em;
    }

    .learning-now-text {
        font-size: 3.5em;
    }

    .learning-now-text {
        font-size: 2.5;
    }

    .learning-now-text-left {
        margin-right: 0em;
    }
    
    .learning-now-text-right {
        margin-left: 0em;
    }

    .socials {
        display: grid;
    }

    .tool {
        top: -6em;
        right: 0;
        margin: auto;
        text-align: center;
    }
}

@media (max-width: 650px) {
    .name-text {
        font-size: 3em;
    }

    .about-me-description {
        font-size: 1em;
    }

    .project-section-title {
        font-size: 2.5rem;
    }
    .learning-now-text {
        font-size: 1.5em;
    }

    .project-page-button{
        font-size: 2em;
    }

    .learning-now-text {
        font-size: 3em;
    }

    .learning-now-text {
        font-size: 2em;
    }

    .learning-now-text-left {
        margin-right: 0em;
    }
    
    .learning-now-text-right {
        margin-left: 0em;
    }

    .learning-slider {
        width: 12em;
    }
}

@media (max-width: 450px) {
    .about-me-description {
        font-size: .8em;
    }

    .name-text{
        font-size: 2em;
    }

    .about-me-name{
        gap: 1.5em;
    }

    .project-section-title {
        font-size: 1.5rem;
    }
    .project-page-button{
        font-size: 1.7em;
    }

    .learning-now-text {
        font-size: 2.7em;
    }
    .learning-now-text {
        font-size: 1.2em;
    }

    .learning-now-text-left {
        margin-right: 0em;
    }
    
    .learning-now-text-right {
        margin-left: 0em;
    }

    .learning-slider {
        width: 10.4em;
    }

    .social-img {
        height: 1.5em;
        width: 1.5em;
    }
}

